html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-weight: 500;
  color: var(--gray);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --cyan: hsl(180, 66%, 49%);
  --dk-violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --gray-violet: hsl(257, 7%, 63%);
  --very-dk-blue: hsl(255, 11%, 22%);
  --very-dk-violet: hsl(260, 8%, 14%);
}

:root {
  --font: "Poppins", sans-serif;
}

.container {
  max-width: 67.5rem;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}